import { request, gql } from 'graphql-request'

export interface IPaint{
    peinture_id:{
        titre:string
        description:string|null
        photo:{
        filename_disk:string
        }
        annee_de_creation:number|null
        technique_de_creation:string|null
        }
  }
export interface IGallery{
    titre:string,
    description:string|null,
    miniature:{
        filename_disk:string
      }
    peintures:Array<IPaint>
    color:string;
    
}
export interface IVideo{
    date_created:string,
    titre:string,
    video_posture:string,
    video_relaxation:string|null
}
export interface IInfo{
    id:number,
    description_art:string,
    description_yoga:string,
    description_index:string,
    description_page_nouvelle_peintures:string,
    nouveaute_plage_annuel:number
}
export interface IInfoYoga{
    fiches_descriptions:string,
    videos_descriptions:string
}
export interface IYoga{
    titre:string,
    description:string,
    thumbnail: {
        filename_disk:string,
    }
    etapes: Array<{
        titre:string,
        description:string,
        postures:Array<{
        titre:string,
        mouvements:Array<{
            description:string,
            etapes_img:Array<{
            directus_files_id:{
                filename_disk:string,
            }
            }>
        }>
        }>
        sub_etapes:Array<{
        titre:string,
        description:string,
        postures:Array<{
            titre:string,
            mouvements:Array<{
            description:string,
            etapes_img:Array<{
            directus_files_id:{
                filename_disk:string,
            }
            }>
            }>
        }>
        }>
    }>
}
export class Gallery{

    

    static async getGallerys():Promise<{
        galerie:Array<IGallery>,
        info:IInfo,
        infos_yoga:IInfoYoga,
        fiche_yoga:any,
        video_yoga:Array<IVideo>
    }>{

        let galerie:Array<IGallery> = []
        let info:IInfo={
            id:0,
            description_art:"",
            description_yoga:"",
            description_index:"",
            description_page_nouvelle_peintures:"",
            nouveaute_plage_annuel:0
        }
        let infoYoga:IInfoYoga = {
            fiches_descriptions: '',
            videos_descriptions: ''
        }
        let yoga: Array<IYoga> =[];
        let videoYoga: Array<IVideo> = []
        const query = gql`
        {
            galerie (filter:{status: {_eq: "published"}}, sort:"date_updated") {
              titre
              description
              status
              color
              miniature{
                  filename_disk
                }
              peintures{
                peinture_id (sort:"annee_de_creation") {
                  titre
                  description
                  photo{
                    filename_disk
                  }
                  annee_de_creation 
                  technique_de_creation
                }
              }
            }
            fiche_yoga (filter:{status: {_eq: "published"}}) {
              titre
              description
              thumbnail {
                filename_disk
              }
              etapes {
                titre
                description
                postures{
                  titre
                  mouvements{
                    description
                    etapes_img{
                      directus_files_id{
                        filename_disk
                      }
                    }
                  }
                }
                sub_etapes{
                  titre
                  description
                  postures{
                    titre
                    mouvements{
                    description
                      etapes_img{
                      directus_files_id{
                        filename_disk}
                      }
                    }
                  }
                }
              }
            }
            infos_generale {
                id
                description_art
                description_yoga
                description_index
                description_page_nouvelle_peintures
                nouveaute_plage_annuel
            }
            infos_yoga {
                id
                fiches_descriptions
                videos_descriptions
            }
            video_yoga (filter:{status: {_eq: "published"}}) {
                date_created
                titre
                video_posture
                video_relaxation
            }
          }
        `;
        await request('https://christineapi.chiliwa.com/graphql', query)
        .then((data:{
            galerie:Array<IGallery>,
            infos_generale:IInfo,
            infos_yoga:IInfoYoga,
            fiche_yoga:Array<IYoga>,
            video_yoga:Array<IVideo>
        }) => {
            galerie=data.galerie;
            info=data.infos_generale;
            yoga = data.fiche_yoga;
            infoYoga = data.infos_yoga;
            videoYoga = data.video_yoga;
            videoYoga.map((v,i) => {
                v.video_posture.split("/").pop() !== undefined 
                && (videoYoga[i].video_posture = (v.video_posture.split("/").pop() as string).split("?v=").pop() as string);
                v.video_relaxation !== null
                && v.video_relaxation.split("/").pop()!== undefined 
                && (videoYoga[i].video_relaxation = (v.video_relaxation.split("/").pop() as string).split("?v=").pop()  as string);
            })
            return {
                galerie:galerie,
                info:info,
                fiche_yoga:yoga,
                infos_Yoga:infoYoga,
                video_yoga:videoYoga
            };
        })
        .catch(er => console.log("error: "+er));
        return {
            galerie:galerie,
            info:info,
            infos_yoga:infoYoga,
            fiche_yoga:yoga,
            video_yoga:videoYoga};
    }
}