import React, { useState } from 'react';
import {IYoga, IInfoYoga} from "../../entity/gallery.class";
import {parseFicheTxt} from "../../functions/parsingTxt";

interface Props{
    id:number,
    name:string,
    content:string,
    fiche:Array<IYoga>,
    infoYoga:IInfoYoga,
    last:boolean
}

const Card:React.FC<Props> = props => {
  const initFiche = (idTarget:number = -1, set:Function = ()=>{})=>{
      const id = idTarget !== -1? idTarget : 0;
      if(!props.fiche[id]) return ;
      const wrapper = document.getElementsByClassName("ficheYogaDetail")[0] as HTMLElement;
      if(idTarget !== -1) wrapper.classList.remove("hideMobile");
      const ficheTarget = props.fiche[id];
      let render = 
      <div className="yogaBox" data-id={id}>
        <h2>{ficheTarget.titre}</h2>
        <p>{ficheTarget.description}</p>
        {ficheTarget.etapes.map((e,eI) =>{
          return(
            <div key={props.id+"etp"+eI}>
              <h3>{e.titre}</h3>
              {parseFicheTxt(e.description).map((p,i) => <p key={`text${i}-${eI}`} className={i===0 ?"first":""}>{p}</p>)}
              {e.sub_etapes.map((se,seI)=>{
                return(
                  <div key={props.id+"subEtp"+seI}>
                    <h4>{se.titre}</h4>
                  </div>
                )
              })}
              {e.postures.map((p,pI)=>{
                return(
                  <div key={props.id+"posture"+pI} >
                    <h4>{p.titre}</h4>
                    {p.mouvements.map((m,mI)=>{
                      return(
                        <div key={props.id+"mouv"+eI+"_"+mI+"_"+pI+"_"+m.etapes_img.length}>
                          {parseFicheTxt(m.description).map((p,i) => <p key={`text${i}-${pI}-${mI}`} className={i===0 ?"first":""}>{p}</p>)}
                          <div className="wrapperImg">
                              {m.etapes_img.map((img,i)=>{
                                return(
                                  <img 
                                  key={props.id+i+img.directus_files_id.filename_disk}
                                  src={`https://christineapi.chiliwa.com/assets/`+img.directus_files_id.filename_disk} 
                                  alt={`${p.titre}-${mI}-${i}`}/>
                                )
                              })}
                          </div>
                        </div>
                      )
                    })}
                  </div>
                )
              })}
            </div>
          )
        })}
        
      </div>;
      //set active
      let elFiches = document.getElementsByClassName(`activeFiche`) as HTMLCollectionOf<HTMLElement> ;
      if(idTarget > -1)for (let iF = 0; iF < elFiches.length; iF++) {
        elFiches[iF].classList.remove("activeFiche");
      }
      idTarget > -1 && (document.getElementById(`ficheYogaID${idTarget}`) as HTMLElement)
        .classList.add("activeFiche")
      //
      if(idTarget !== -1) setfiche(render) ;else return render;
  }
  const [fiche, setfiche] = useState(initFiche());

  
  
  return (
    <section key={props.name+props.id} id={"yoga"+props.id} className="ficheYogaSection yogaSection">
    
    <div className="ficheYogaWrapper">
      {props.fiche.map((f,i) => {
        return(
          <div 
          key={"ficheYoga"+i} 
          className={"ficheYoga "+ (i === 0?"activeFiche":"")}
          id={"ficheYogaID"+i}
          onClick={()=>{initFiche(i,setfiche)}}
          >
            <span 
            style={{background: `url("${`https://christineapi.chiliwa.com/assets/`+f.thumbnail.filename_disk}")`,backgroundSize: "contain",backgroundRepeat:"no-repeat",backgroundPosition:"center"}}
            ></span>
            <p>{f.titre}</p>
          </div>
        );
      })}
      <div className="ficheYogaDetail hideMobile" id="ficheYogaDetailPanel">
        <span
        className="hideOnPc closeFiche"
        onClick={ () => {
          (document.getElementsByClassName("ficheYogaDetail")[0] as HTMLElement)
            .classList.add("hideMobile");
        }}>✖</span>
        {fiche}
      </div>
    </div>
    <div className="yogaIndexInfo yogaCardInfo yogaBoxInvert">
      <h1>{props.name}</h1>
      <p>{props.infoYoga.fiches_descriptions}</p>
      <div className="navYogaFiche">
        <a href={"#index"} className="BtnPrev"><i className="fas fa-angle-double-up"></i></a>
        <a href={"#yoga"+(props.id+1)} className={props.last?"BtnNext hide":"BtnNext "}><i className="fas fa-arrow-down"></i></a>
      </div>
      
    </div>
  </section>
  );
}

export default Card;
