import React, { useState } from 'react';
import {IInfoYoga, IVideo} from "../../entity/gallery.class"
interface Props{
    id:number,
    name:string,
    content:string,
    infoYoga:IInfoYoga,
    videoYoga:Array<IVideo>,
    last:boolean 
}
const Video:React.FC<Props> = props => {
  const [videoLink, setvideoLink] = useState({
    posture:props.videoYoga[0].video_posture,
    relaxation:"none",
    active:props.videoYoga[0].video_posture
  });
  const [show, setShow] = useState(false);
  return (
    <section key={props.name+props.id} id={"yoga"+props.id} className="VideoYogaSection yogaSection">
    <div className={"yogaIndexInfo yogaVideoInfo yogaBox "+(show?"show":"")}>
      <h1>{props.name}</h1>
      <div className="videoYogaContent">
        <p>{props.infoYoga.videos_descriptions}</p>
        <div className="videoLinkYogaWrapper">
        {props.videoYoga.map((video,i)=>{return(
          <div 
          key={`videoLinks${i}`} 
          className="videoLinkYoga" 
          onClick={()=>{
            setvideoLink({
                posture:video.video_posture,
                relaxation:video.video_relaxation?video.video_relaxation:"none",
                active:video.video_posture
              })
            }
          }>
            <p>{(i+1)+" - "+video.titre}</p>
          </div>
        )})}
      </div>
      </div>
      
      <div className="navYogaFiche">
        <a href={props.id !== 0?"#yoga"+(props.id-1):"#index"} className="BtnPrev"><i className="fas fa-arrow-up"></i></a>
        <a href={"#index"} className="BtnPrev"><i className="fas fa-angle-double-up"></i></a>
        <div className="mobileShow"
        onClick={()=>{setShow(!show)}}
        ><i className="fas fa-list"></i></div>
      </div>
    </div>
    <div className="videoWrapper">
      <iframe 
      title="cours de yoga"
      width="560" 
      height="315" 
      src={`https://www.youtube.com/embed/${videoLink.active}`} 
      ></iframe>
      <div className="videoLinkYogaBtn">
        <button className="btnLinkYoga" onClick={()=>{
          setvideoLink({
            posture:videoLink.posture,
            relaxation:videoLink.relaxation,
            active:videoLink.posture
          })
        }}>Posture</button>
        {videoLink.relaxation !== "none" 
        && <button className="btnLinkYoga" onClick={()=>{
          setvideoLink({
            posture:videoLink.posture,
            relaxation:videoLink.relaxation,
            active:videoLink.relaxation
          })
        }}>Relaxation</button>}
      </div>
      
    </div>
  </section>
  );
}

export default Video;
