

export const parseFicheTxt = (txt:string):Array<string> => {
    let render:string = ""
    let renderList:Array<string> = []
    for (let i = 0; i < txt.length; i++) {
        if ( // retour à la ligne
            txt[i] === ":" 
        ||  txt[i] === "."
        ){
            render += txt[i];
            renderList.push(render);
            render = "";
        }
        else render += txt[i];
    }
    render !== "" && renderList.push(render);
    return renderList;
}